@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;

  @media (max-width: 768px) {
    padding: 20px 0;

    .button--cart {
      padding: 6px 12px;

      span {
        font-size: 14px;
      }
    }
  }

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    display: flex;
  

    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }

    @media (max-width: 786px) {
      h1 {
        font-size: 18px;
      }

      p {
        font-size: 14px;
      }
    }

    @media (max-width: 991px) {
      h1, p {
        display: none;
      }
    }

  }
}
